
import React, { useEffect, useState, useRef } from 'react';
import { motion, useAnimation } from 'framer-motion';
import { ArrowUpRight, TrendingUp, Target, ChevronLeft, ChevronRight } from 'lucide-react';

const ORIGINAL_CASE_STUDIES = [
    {
        id: 1,
        client: "Nebula Finance",
        industry: "FinTech & Trading",
        challenge: "Legacy infrastructure caused 40% latency.",
        solution: "Distributed micro-latency ledger system.",
        metrics: [
            { label: "Speed", value: "10x" },
            { label: "Revenue", value: "+$2.4M" }
        ],
        graphColor: "text-emerald-500",
        accent: "bg-emerald-500"
    },
    {
        id: 2,
        client: "Vortex Retail",
        industry: "Global E-Commerce",
        challenge: "Stagnant conversion rates (1.2%).",
        solution: "AI personalization engine.",
        metrics: [
            { label: "Conversion", value: "4.8%" },
            { label: "Growth", value: "+215%" }
        ],
        graphColor: "text-blue-500",
        accent: "bg-blue-500"
    },
    {
        id: 3,
        client: "Apex Health",
        industry: "Healthcare SaaS",
        challenge: "Data inaccessible in low-bandwidth.",
        solution: "Offline-first PWA with sync.",
        metrics: [
            { label: "Adoption", value: "+300%" },
            { label: "Clinics", value: "50k+" }
        ],
        graphColor: "text-rose-500",
        accent: "bg-rose-500"
    },
    {
        id: 4,
        client: "Cyber Shield",
        industry: "Cybersecurity",
        challenge: "Zero-day defenses failing.",
        solution: "AI-driven threat prediction.",
        metrics: [
            { label: "Detection", value: "99.9%" },
            { label: "Response", value: "<1ms" }
        ],
        graphColor: "text-purple-500",
        accent: "bg-purple-500"
    }
];

// Create a tripled list for seamless looping [Clone, Real, Clone]
const CASE_STUDIES = [...ORIGINAL_CASE_STUDIES, ...ORIGINAL_CASE_STUDIES, ...ORIGINAL_CASE_STUDIES];

const CARD_WIDTH = 450; // Width of card
const GAP = 32; // Gap between cards

const CaseStudies: React.FC = () => {
    const [currentIndex, setCurrentIndex] = useState(ORIGINAL_CASE_STUDIES.length);
    const controls = useAnimation();
    const containerRef = useRef<HTMLDivElement>(null);
    const [isAnimating, setIsAnimating] = useState(false);

    // Initial positioning
    useEffect(() => {
        controls.set({ x: -currentIndex * (CARD_WIDTH + GAP) });
    }, []);

    const handleNext = async () => {
        if (isAnimating) return;
        setIsAnimating(true);
        const nextIndex = currentIndex + 1;
        setCurrentIndex(nextIndex);
        await controls.start({ x: -nextIndex * (CARD_WIDTH + GAP), transition: { duration: 0.5, ease: "easeInOut" } });

        // Loop Check
        if (nextIndex >= ORIGINAL_CASE_STUDIES.length * 2) {
            const resetIndex = ORIGINAL_CASE_STUDIES.length;
            setCurrentIndex(resetIndex);
            controls.set({ x: -resetIndex * (CARD_WIDTH + GAP) });
        }
        setIsAnimating(false);
    };

    const handlePrev = async () => {
        if (isAnimating) return;
        setIsAnimating(true);
        const prevIndex = currentIndex - 1;
        setCurrentIndex(prevIndex);
        await controls.start({ x: -prevIndex * (CARD_WIDTH + GAP), transition: { duration: 0.5, ease: "easeInOut" } });

        // Loop Check
        if (prevIndex < ORIGINAL_CASE_STUDIES.length) {
            const resetIndex = ORIGINAL_CASE_STUDIES.length * 2 - 1;
            setCurrentIndex(resetIndex);
            controls.set({ x: -resetIndex * (CARD_WIDTH + GAP) });
        }
        setIsAnimating(false);
    };

    return (
        <section className="py-24 bg-background overflow-hidden">
            <div className="max-w-[1920px] mx-auto px-6 md:px-12 flex flex-col xl:flex-row gap-16 items-center xl:items-start">

                {/* Fixed Header Content */}
                <div className="xl:w-[500px] flex-shrink-0 flex flex-col items-center xl:items-start text-center xl:text-left z-10 bg-background py-8">
                    <div className="inline-flex items-center gap-2 px-4 py-1.5 rounded-full bg-brand-medium/10 border border-brand-medium/20 mb-8">
                        <TrendingUp className="w-4 h-4 text-brand-medium" />
                        <span className="text-xs font-bold text-brand-medium uppercase tracking-widest">Growth Engines</span>
                    </div>

                    <h2 className="text-5xl md:text-7xl font-black text-foreground tracking-tighter leading-tight mb-6">
                        Where Strategy Meets <br />
                        <span className="text-transparent bg-clip-text bg-gradient-to-r from-brand-medium to-brand-cyan">Measurable Results.</span>
                    </h2>

                    <p className="text-xl text-slate-600 dark:text-slate-400 leading-relaxed max-w-md mb-12">
                        Browse our archive of digital breakthroughs. Every metric represents a milestone in our partners' growth success.
                    </p>

                    {/* Navigation Buttons */}
                    <div className="flex items-center gap-4">
                        <button
                            onClick={handlePrev}
                            className="w-14 h-14 rounded-full border border-slate-200 dark:border-white/10 flex items-center justify-center hover:bg-slate-100 dark:hover:bg-white/5 transition-colors group"
                        >
                            <ChevronLeft className="w-6 h-6 text-slate-600 dark:text-slate-400 group-hover:text-brand-medium transition-colors" />
                        </button>
                        <button
                            onClick={handleNext}
                            className="w-14 h-14 rounded-full bg-foreground text-background flex items-center justify-center hover:bg-brand-medium hover:text-white transition-colors"
                        >
                            <ChevronRight className="w-6 h-6" />
                        </button>
                    </div>
                </div>

                {/* Infinite Carousel Track */}
                <div ref={containerRef} className="w-full overflow-hidden xl:mask-fade-left">
                    <motion.div
                        animate={controls}
                        className="flex gap-8 items-stretch"
                    >
                        {CASE_STUDIES.map((study, index) => (
                            <div
                                key={`${study.id}-${index}`}
                                className="flex-shrink-0 w-[350px] md:w-[450px] rounded-[2.5rem] bg-slate-50 dark:bg-[#0b0f1a] border border-slate-200 dark:border-white/5 p-8 flex flex-col justify-between hover:border-brand-medium/30 transition-colors group"
                            >
                                <div>
                                    <div className="flex justify-between items-start mb-8">
                                        <div className={`w-12 h-12 rounded-xl ${study.accent} bg-opacity-10 flex items-center justify-center`}>
                                            <Target className={`w-6 h-6 ${study.graphColor}`} />
                                        </div>
                                        <button className="p-2 rounded-full hover:bg-slate-200 dark:hover:bg-white/10 transition-colors">
                                            <ArrowUpRight className="w-5 h-5 text-slate-400 group-hover:text-brand-medium" />
                                        </button>
                                    </div>

                                    <h3 className="text-2xl font-bold text-foreground mb-1">{study.client}</h3>
                                    <p className="text-xs font-bold text-slate-400 uppercase tracking-widest mb-6">{study.industry}</p>

                                    <div className="space-y-4 mb-8">
                                        <div className="p-4 rounded-xl bg-white dark:bg-black/20 border border-slate-100 dark:border-white/5">
                                            <p className="text-xs font-bold text-rose-500 uppercase tracking-widest mb-1">Challenge</p>
                                            <p className="text-sm text-slate-600 dark:text-slate-400 leading-snug">{study.challenge}</p>
                                        </div>
                                        <div className="p-4 rounded-xl bg-white dark:bg-black/20 border border-slate-100 dark:border-white/5">
                                            <p className="text-xs font-bold text-emerald-500 uppercase tracking-widest mb-1">Solution</p>
                                            <p className="text-sm text-slate-600 dark:text-slate-400 leading-snug">{study.solution}</p>
                                        </div>
                                    </div>
                                </div>

                                <div className="pt-6 border-t border-slate-200 dark:border-white/5 flex items-center justify-between">
                                    {study.metrics.map((metric, i) => (
                                        <div key={i}>
                                            <div className="text-2xl font-black text-foreground">{metric.value}</div>
                                            <div className="text-[10px] uppercase font-bold text-slate-500">{metric.label}</div>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        ))}
                    </motion.div>
                </div>
            </div>

            <style jsx global>{`
                .xl\\:mask-fade-left {
                    @media (min-width: 1280px) {
                        mask-image: linear-gradient(to right, transparent, black 10%, black 100%);
                    }
                }
            `}</style>
        </section>
    );
};

export default CaseStudies;
