
import React, { useRef, useMemo, memo } from 'react';
import { Canvas, useFrame } from '@react-three/fiber';
import { Float, Sphere, Points, PointMaterial, Preload, Ring } from '@react-three/drei';
import * as THREE from 'three';

interface ParticleRingProps {
  radius?: number;
  count?: number;
  color?: string;
  speed?: number;
}

const ParticleRing = memo(({ radius = 2, count = 200, color = "#6366f1", speed = 0.2 }: ParticleRingProps) => {
  const points = useMemo(() => {
    const p = new Float32Array(count * 3);
    for (let i = 0; i < count; i++) {
      const theta = Math.random() * Math.PI * 2;
      // Distribute points on a ring with some thickness
      const r = radius + (Math.random() - 0.5) * 0.2;
      p[i * 3] = r * Math.cos(theta);
      p[i * 3 + 1] = (Math.random() - 0.5) * 0.1; // Flatness
      p[i * 3 + 2] = r * Math.sin(theta);
    }
    return p;
  }, [count, radius]);

  const ref = useRef<THREE.Points>(null!);

  useFrame((state) => {
    if (ref.current) {
      ref.current.rotation.y += state.clock.getDelta() * speed;
      ref.current.rotation.z = Math.sin(state.clock.getElapsedTime() * 0.2) * 0.1;
    }
  });

  return (
    <Points ref={ref} positions={points} stride={3} frustumCulled={true}>
      <PointMaterial
        transparent
        color={color}
        size={0.03}
        sizeAttenuation={true}
        depthWrite={false}
        opacity={0.6}
        blending={THREE.AdditiveBlending}
      />
    </Points>
  );
});

const CoreSphere = memo(() => {
  const meshRef = useRef<THREE.Mesh>(null!);

  useFrame((state) => {
    if (meshRef.current) {
      meshRef.current.rotation.y = state.clock.getElapsedTime() * 0.2;
      meshRef.current.rotation.x = state.clock.getElapsedTime() * 0.1;
    }
  });

  return (
    <Sphere ref={meshRef} args={[1.2, 32, 32]} scale={1}>
      <meshStandardMaterial
        color="#1e1b4b"
        emissive="#312e81"
        emissiveIntensity={0.5}
        wireframe
        transparent
        opacity={0.3}
      />
    </Sphere>
  );
});

const GlowingCore = memo(() => {
  return (
    <Sphere args={[0.8, 32, 32]}>
      <meshBasicMaterial color="#4f46e5" transparent opacity={0.8} />
    </Sphere>
  )
});


const ThreeSphereScene: React.FC = () => {
  return (
    <div className="absolute inset-0 z-0 opacity-100 pointer-events-none">
      <Canvas
        camera={{ position: [0, 0, 6], fov: 50 }}
        gl={{
          antialias: true,
          powerPreference: "high-performance",
          alpha: true
        }}
        dpr={[1, 2]}
      >
        <ambientLight intensity={0.5} />
        <pointLight position={[10, 10, 10]} intensity={1} color="#818cf8" />
        <pointLight position={[-10, -10, -10]} intensity={0.5} color="#c084fc" />

        <Float speed={2} rotationIntensity={0.2} floatIntensity={0.5}>
          <CoreSphere />
          <GlowingCore />
          <ParticleRing radius={2.0} count={300} color="#818cf8" speed={0.1} />
          <ParticleRing radius={2.8} count={400} color="#c084fc" speed={-0.15} />
          <ParticleRing radius={3.5} count={200} color="#38bdf8" speed={0.05} />
        </Float>

        <Preload all />
      </Canvas>
    </div>
  );
};

export default memo(ThreeSphereScene);
