
import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Menu, X, ArrowUpRight, Sun, Moon } from 'lucide-react';

interface NavbarProps {
  isDark: boolean;
  toggleTheme: () => void;
}

const Navbar: React.FC<NavbarProps> = ({ isDark, toggleTheme }) => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => setIsScrolled(window.scrollY > 20);
    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const links = [
    { name: 'Capabilities', href: '#services' },
    { name: 'Assets', href: '#tools' },
    { name: 'Archives', href: '#portfolio' },
    { name: 'Genesis', href: '#process' },
  ];

  return (
    <nav className={`fixed top-0 left-0 w-full z-[100] transition-all duration-500 ${isScrolled ? 'py-4' : 'py-8'}`}>
      <div className="max-w-7xl mx-auto px-6">
        <div className={`relative glass-morphism rounded-full px-8 py-4 flex items-center justify-between transition-all duration-500 ${isScrolled ? 'border-brand-medium/20 shadow-[0_10px_40px_rgba(0,0,0,0.5)]' : 'border-transparent'}`}>
          <a href="#" className="flex items-center gap-3 group clickable" data-cursor="HOME">
            <div className="w-10 h-10 bg-brand-deep rounded-full flex items-center justify-center group-hover:rotate-[360deg] transition-transform duration-700">
              <span className="text-white font-black text-xl">P</span>
            </div>
            <span className={`text-xl font-black uppercase tracking-tighter ${isDark ? 'text-white' : 'text-slate-900'}`}>
              PREET<span className="text-brand-sky">TECH.</span>
            </span>
          </a>

          <div className="hidden lg:flex items-center gap-10">
            {links.map((link) => (
              <a
                key={link.name}
                href={link.href}
                className={`text-[10px] font-black uppercase tracking-[0.3em] ${isDark ? 'text-slate-400 hover:text-white' : 'text-slate-500 hover:text-brand-deep'} transition-colors clickable`}
                data-cursor="NAV"
              >
                {link.name}
              </a>
            ))}
          </div>

          <div className="flex items-center gap-4">
            <button
              onClick={toggleTheme}
              className={`p-2 rounded-full transition-colors ${isDark ? 'text-slate-400 hover:text-white hover:bg-white/10' : 'text-slate-600 hover:text-brand-deep hover:bg-slate-200'}`}
              aria-label="Toggle Theme"
            >
              {isDark ? <Sun className="w-5 h-5" /> : <Moon className="w-5 h-5" />}
            </button>

            <button className="hidden sm:flex items-center gap-2 px-6 py-2.5 bg-white text-black rounded-full font-black text-[10px] uppercase tracking-widest hover:bg-brand-medium hover:text-white transition-all clickable">
              Provision <ArrowUpRight className="w-3 h-3" />
            </button>
            <button
              className={`lg:hidden p-2 clickable ${isDark ? 'text-white' : 'text-slate-900'}`}
              onClick={() => setIsOpen(!isOpen)}
            >
              {isOpen ? <X /> : <Menu />}
            </button>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
