"use client";

import React, { useState } from 'react';
import Navbar from '../components/Navbar';
import Hero from '../components/Hero';
import Services from '../components/Services';
import WhyChooseUs from '../components/WhyChooseUs';
import CaseStudies from '../components/CaseStudies';
import SimpleSteps from '../components/SimpleSteps';
import ReadyToPartner from '../components/ReadyToPartner';
import Testimonials from '../components/Testimonials';
import Insights from '../components/Insights';
import ContactCTA from '../components/ContactCTA';
import Footer from '../components/Footer';

enum Theme {
    DARK = 'dark',
    LIGHT = 'light',
}

export default function Home() {
    const [theme, setTheme] = useState<Theme>(Theme.DARK);

    const toggleTheme = () => {
        const newTheme = theme === Theme.DARK ? Theme.LIGHT : Theme.DARK;
        setTheme(newTheme);

        // Update document class for Tailwind dark mode
        if (newTheme === Theme.DARK) {
            document.documentElement.classList.add('dark');
        } else {
            document.documentElement.classList.remove('dark');
        }
    };

    // Initialize theme on mount
    React.useEffect(() => {
        document.documentElement.classList.add('dark');
    }, []);

    return (
        <main className="relative z-10 selection:bg-brand-medium/30 overflow-x-hidden bg-background text-foreground transition-colors duration-300">
            <Navbar isDark={theme === Theme.DARK} toggleTheme={toggleTheme} />

            <Hero />

            <Services />

            <WhyChooseUs />

            <CaseStudies />

            <Insights />

            <SimpleSteps />

            <ReadyToPartner />

            <Testimonials />

            <ContactCTA />

            <Footer />
        </main>
    );
}
